<template>
  <div>
    <h1 style="text-align: center">目前在:{{ page }}頁</h1>

    <el-table :data="pagedTableData" style="width: 100%">
      <el-table-column
        align="center"
        fixed
        prop="customer"
        label="客戶"
        width="80"
      >
      </el-table-column>

      <el-table-column align="center" prop="title" label="寶號" width="120">
      </el-table-column>
      <el-table-column align="center" prop="address" label="地址" width="120">
      </el-table-column>
      <el-table-column align="center" prop="tel" label="電話" width="120">
      </el-table-column>
      <el-table-column align="center" prop="fax" label="傳真" width="120">
      </el-table-column>

      <el-table-column align="center" prop="contact" label="聯絡人" width="120">
      </el-table-column>
      <el-table-column
        align="center"
        prop="cellphone"
        label="行動電話"
        width="120"
      >
      </el-table-column>
      <el-table-column
        align="center"
        prop="taxnumber"
        label="統一編號"
        width="120"
      >
      </el-table-column>
      <el-table-column align="center" fixed="right" label="動作" width="200">
        <template slot-scope="scope">
          <el-row>
            <el-button
              @click="showEditDialog(scope.row)"
              type="success"
              size="small"
              >編輯</el-button
            >

            <el-button type="danger" @click="deleteCust(scope.row)" size="small"
              >刪除</el-button
            >
          </el-row>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="修改客戶" :visible.sync="editDialogVisible" width="30%">
      <!--內容主體區域-->
      <el-form :model="editForm" label-width="70px">
        <!-- <el-form-item label="id" prop="customerid">
          <el-input v-model="editForm.customerid" :disabled="true"></el-input>
        </el-form-item> -->
        <el-form-item label="客戶" prop="customer">
          <el-input v-model="editForm.customer" disabled></el-input>
        </el-form-item>
        <el-form-item label="寶號" prop="title">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="address">
          <el-input v-model="editForm.address"></el-input>
        </el-form-item>
        <el-form-item label="電話" prop="tel">
          <el-input v-model="editForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="傳真" prop="fax">
          <el-input v-model="editForm.fax"></el-input>
        </el-form-item>
        <el-form-item label="聯絡人" prop="contact">
          <el-input v-model="editForm.contact"></el-input>
        </el-form-item>
        <el-form-item label="行動電話" prop="cellphone">
          <el-input v-model="editForm.cellphone"></el-input>
        </el-form-item>
        <el-form-item label="統一編號" prop="taxnumber">
          <el-input v-model="editForm.taxnumber"></el-input>
        </el-form-item>
      </el-form>
      <!--底部按鈕區域-->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">確 定</el-button>
      </span>
    </el-dialog>

    <el-pagination
      layout="prev, pager, next"
      :total="this.tableData.length"
      @current-change="setPage"
    >
    </el-pagination>
  </div>
</template>
<script>
import axios from "axios";
export default {
  created() {
    this.getCustomer();
  },
  data() {
    return {
      show: "false",
      tableData: [
        // 3
      ],
      custForm: {
        customername: "",
        title: "",
        address: "",
        tel: "",
        fax: "",
        contact: "",
        phone: "",
        taxnumber: "",
      },
      page: 1,
      pageSize: 10,
      search: "",
      editDialogVisible: false,
      editForm: {
        customer: "",
        title: "",
        address: "",
        tel: "",
        fax: "",
        contact: "",
        cellphone: "",
        taxnumber: "",
      },
    };
  },
  computed: {
    pagedTableData() {
      return this.tableData.slice(
        this.pageSize * this.page - this.pageSize,
        this.pageSize * this.page
      );
    },
  },

  methods: {
    setPage(val) {
      this.page = val;
    },
    getCustomer() {
      axios({
        method: "get",

        url: "/public/api/customer",
        headers: {
          token: "",
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
        },
      })
        .then((result) => {
          var custArray = result.data;
          custArray.forEach((element) => {
            this.tableData.push({
              customerid: element.customerid,
              customer: element.customer,
              title: element.title,
              address: element.address,
              tel: element.tel,
              fax: element.fax,
              contact: element.contact,
              cellphone: element.cellphone,
              taxnumber: element.taxnumber,
            });
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async deleteCust(val) {
      console.log(val.customerid);
      const confirmResult = await this.$confirm(
        "此操作將永久刪除該使用者, 是否繼續?",
        "提示",
        {
          confirmButtonText: "確定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult == "confirm") {
        console.log("ok");
        axios({
          url: `/public/api/customer/${val.customerid}`,
          method: "delete",
        })
          .then((res) => {
            console.log(res);
            this.$router.replace({ path: "/" });
            if (res.status === 204) {
              this.$message({
                message: "刪除使用者成功",
                type: "success",
              });
            } else {
              this.$message.error("刪除使用者失敗");
            }
          })
          .catch((err) => {
            this.$message.error("刪除使用者異常");
            console.loge(err);
          });
      }
    },
    showEditDialog(userinfo) {
      this.editDialogVisible = true;
      console.log(userinfo);
      this.editForm = userinfo;
    },
    editUser() {
      // axios
      //   .put(`/public/api/customer/1`, this.editForm)
      axios({
        url: `/public/api/customer/${this.editForm.customerid}`,
        method: "put",
        headers: {
          token: "",
          "Content-Type": "application/json;charset=UTF-8",
          "Access-Control-Allow-Origin": "*",
        },
        data: this.editForm,
      })
        .then((res) => {
          console.log(this.editForm);
          console.log(res);
          if (res.status === 200) {
            this.editDialogVisible = false;
            // this.getUserList();
            this.$message({
              message: "修改使用者成功",
              type: "success",
            });
          } else {
            this.$message.error("修改使用者失敗");
          }
        })
        .catch((err) => {
          this.$message.error("修改使用者異常");
          console.log(this.editForm);
          console.log(err);
        });
    },
  },
};
</script>
<style></style>
